<template>
  <div class="step_three">
    <div class="container">
    Imię*<input id="name" v-model="personalData.name">
    Nazwisko*<input id="surname" v-model="personalData.surname">
    Ulica Budynek/Mieszkanie*<input id="adress" v-model="personalData.adress">
    Kod Pocztowy* <input id="postalCode" v-model="personalData.postalCode">
    Miasto*<input id="city" v-model="personalData.city">
    Telefon<input id="phone" v-model="personalData.phone">
    Aders mailowy*<input id="email" v-model="personalData.email"> * Pola wymagane
    </div>
    <div class="nav">
    <router-link to="/home/step_two_summary">
      <button>← Wstecz</button>
    </router-link>
    <div class="right_nav">
        <button v-if="!emptyValidation()" @click="errorEmptyValidation(); ">Uzupełnij Wszystkie Pola</button>
        <button v-if="emptyValidation() && !typeValidation()" @click="errorValidation()">Popraw Pola</button>
        <router-link v-if="emptyValidation() && typeValidation()" to="/home/step_three_addition"><button @click="updatePersonalData()">
        Złóż Zamówienie →
        </button></router-link>
      </div>
  </div></div>
</template>
<script>
export default {
  name: 'StepThree',
  data: () => ({
    personalData: {
      name: '',
      surname: '',
      adress: '',
      postalCode: '',
      city: '',
      phone: '',
      email: ''
    },
    error: {
      name: false,
      surname: false,
      adress: false,
      postalCode: false,
      city: false,
      phone: false,
      email: false
    }
  }),
  methods: {
    updatePersonalData () {
      this.$store.state.PersonalData = this.personalData
    },
    errorEmptyValidation () {
      if (!this.personalData.name) {
        document.getElementById('name').style.background = 'tomato'
      } else {
        document.getElementById('name').style.background = 'white'
      }
      if (!this.personalData.surname) {
        document.getElementById('surname').style.background = 'tomato'
      } else {
        document.getElementById('surname').style.background = 'white'
      }
      if (!this.personalData.postalCode) {
        document.getElementById('postalCode').style.background = 'tomato'
      } else {
        document.getElementById('postalCode').style.background = 'white'
      }
      if (!this.personalData.city) {
        document.getElementById('city').style.background = 'tomato'
      } else {
        document.getElementById('city').style.background = 'white'
      }
      if (!this.personalData.email) {
        document.getElementById('email').style.background = 'tomato'
      } else {
        document.getElementById('email').style.background = 'white'
      }
      if (!this.personalData.adress) {
        document.getElementById('adress').style.background = 'tomato'
      } else {
        document.getElementById('adress').style.background = 'white'
      }
    },
    emptyValidation () {
      return this.personalData.postalCode && this.personalData.name && this.personalData.surname && this.personalData.adress && this.personalData.city && this.personalData.email
    },
    errorValidation () {
      if (this.error.name) {
        alert('Popraw imie')
        document.getElementById('name').style.background = 'tomato'
      }
      if (this.error.surname) {
        alert('Popraw nazwisko')
        document.getElementById('surname').style.background = 'tomato'
      }
      if (this.error.city) {
        alert('Popraw miasto')
        document.getElementById('city').style.background = 'tomato'
      }
      if (this.error.email) {
        alert('Popraw e-mail')
        document.getElementById('email').style.background = 'tomato'
      }
      if (this.error.adress) {
        document.getElementById('adress').style.background = 'tomato'
        alert('Popraw Adress')
      }
      if (this.error.phone) {
        document.getElementById('phone').style.background = 'tomato'
        alert('Popraw numer telefonu')
      }
    },
    typeValidation () {
      const phoneValidation = this.personalData.phone.length === 9 || this.personalData.phone.length === 0
      const postalCodeValidation = /^\d\d-\d\d\d$/
      const numberValidation = /^[0-9]+$/
      const letterValidation = /^[A-Za-z]+$/
      const emailValidation = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      if (!phoneValidation || !emailValidation.test(this.personalData.email) || !letterValidation.test(this.personalData.name) || !letterValidation.test(this.personalData.surname) || !letterValidation.test(this.personalData.city) || !postalCodeValidation.test(this.personalData.postalCode)) {
        if (!letterValidation.test(this.personalData.name)) {
          this.error.name = true
        } else {
          this.error.name = false
        }
        if (!letterValidation.test(this.personalData.surname)) {
          this.error.surname = true
        } else {
          this.error.surname = false
        }
        if (!letterValidation.test(this.personalData.city)) {
          this.error.city = true
        } else {
          this.error.city = false
        }
        if (!emailValidation.test(this.personalData.email)) {
          this.error.email = true
        } else {
          this.error.email = false
        }
        if (!emailValidation.test(this.personalData.postalCode)) {
          this.error.postalCode = true
        } else {
          this.error.postalCode = false
        }
        if (!phoneValidation) {
          this.error.phone = true
        } else {
          this.error.phone = false
        }
        return false
      } else {
        if (this.personalData.postalCode.length === 6) {
          switch (this.personalData.phone.length) {
            case 0:
              return true
            case 9:
              if (numberValidation.test(this.personalData.phone)) {
                this.error.phone = false
                return true
              } else {
                this.error.phone = true
                return false
              }
          }
        } else {
          this.error.postalCode = true
          return false
        }
      }
    }
  }
}
</script>
<style lang="sass" scoped>
.step_three
  z-index: 12
input
  margin: 10px
  border-radius: 25px
  border: none
  padding: 5px
.container
  display: grid
  justify-content: center
  font-weight: bold
</style>
